export default {
	data() {
		return {};
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	methods: {
		onSubmit(e) {
			this.$Fetch({
				url: 'advise/add',
				data: e.detail.value,
				success: res => {
					uni.showToast({
						title: '提交成功！',
						icon: 'success'
					});
					setTimeout(() => {
						uni.navigateBack({});
					}, 1500);
				}
			});
		}
	}
};
